.productSelector {
  max-width: 360px;
  background-color: #ffffff;
  border-radius: 1em;
  overflow: hidden;
}
.productSelector .slider {
  background-color: #f6f7fb;
  padding: 1em 0.5em;
  gap: 1em;
  display: flex;
  flex-direction: column;
}
.productSelector .sliderItem :global .rc-slider-rail {
  background-color: #ffffff;
}
.productSelector .button {
  margin: auto;
  display: block;
  width: fit-content;
}
.productSelector .buttonHolder {
  padding: 0.5em;
}
.productSelector .buttonHolder :global .simple-link {
  color: #ffffff;
  background-color: #e3a404;
}
.productSelector .buttonHolder :global .simple-link:hover {
  background-color: #bd8d16;
}